import React, { useState } from 'react';

const WordForm = ({ createWord  }) => {
  const [newWord, setNewWord] = useState('');
  const [newWordDkDefinition, setWewWordDkDefinition] = useState('');

  const handleChange = (event) => {
    setNewWord(event.target.value);
  };

  const handleDkDefinitionChange = (event) => {
    setWewWordDkDefinition(event.target.value);
  };

  const addWord = (event) => {
    event.preventDefault();

    createWord({
      lex: newWord,
      definitionDk: newWordDkDefinition,
      createdate: new Date().toISOString(),
      updatedate: new Date().toISOString(),
    });

    setNewWord('');
    setWewWordDkDefinition('');
  };

  return (
    <div className="formDiv">
      <h2>Create a new word</h2>

      <form onSubmit={addWord}>
        <input id="newWordInput" value={newWord} onChange={handleChange} />
        <input id="newWordDefinitionDk" value={newWordDkDefinition} onChange={handleDkDefinitionChange} />
        <button id="newWordButton" type="submit">save</button>
      </form>
    </div>
  );
};

export default WordForm;
