import Footer from './components/Footer';
import React, { useState, useEffect, useRef } from 'react';
import Notification from './components/Notification';
import Togglable from './components/Togglable';
import LoginForm from './components/LoginForm';
import WordForm from './components/WordForm';

import loginService from './services/login';
import wordService from './services/words';

function App() {

  const [words, setWords] = useState([]);

  const [notificationMessage, setNotificationMessage] = useState('');
  const [notificationType, setNotificationType] = useState('');
  const [user, setUser] = useState(null);

  const wordFormRef = useRef();
  useEffect(() => {
    wordService.getAll().then((initialWords) => {
      setWords(initialWords);
    });
  }, []);

  useEffect(() => {
    const loggedUserJSON = window.sessionStorage.getItem('loggedNoteappUser');
    if (loggedUserJSON) {
      console.log('loggedUserJSON', loggedUserJSON);
      const user = JSON.parse(loggedUserJSON);
      setUser(user);
      wordService.setToken(user.token);
    }
  }, []);

  const addWord = (wordObject) => {
    wordFormRef.current.toggleVisibility();

    wordService.create(wordObject).then((returnedWord) => {
      setWords(words.concat(returnedWord));
    });
  };

  const handleLogin = async (userObject) => {
    let username = userObject.username;
    let password = userObject.password;

    try {
      const user = await loginService.login({
        username,
        password,
      });

      window.sessionStorage.setItem('loggedNoteappUser', JSON.stringify(user));

      wordService.setToken(user.token);
      setUser(user);
    } catch (exception) {
      notify('wrong credentials', 'error');
    }
  };

  const loginForm = () => {
    return (
      <Togglable buttonLabel="login">
        <LoginForm handleSubmit={handleLogin} />
      </Togglable>
    );
  };

  const logout = () => {
    window.sessionStorage.removeItem('loggedNoteappUser');

    notify(`Bye, ${user.name}`, 'success');

    wordService.setToken(null);
    setUser(null);
    loginForm();
  };

  const notify = (notificationMessage, notificationType) => {
    setNotificationMessage(notificationMessage);
    setNotificationType(notificationType);

    setTimeout(() => {
      setNotificationMessage('');
      setNotificationType('');
    }, 5000);
  };

  const wordForm = () => (
    <Togglable buttonLabel="new word" ref={wordFormRef}>
      <WordForm createWord={addWord} />
    </Togglable>
  );

  return (
    <div>
      <h1>Wehee</h1>
      <Notification message={notificationMessage} type={notificationType} />


      {user === null ? (
        loginForm()
      ) : (
        <div>
          <p>
            {user.name} logged in
            <button onClick={() => logout()}> logout </button>
          </p>

          {wordForm()}
        </div>
      )}

      <ul>
        <li>rydde op - at ordne forskellige ting p√• deres rette plads</li>
        <li>sammenligne - to compare</li>
        <li>forskellie - lige some anderledes men forskelligt</li>
      </ul>

      <ul>
        {words.map((word, i) => (
          <li key={i} >
            {word.lex}
          </li>
        ))}
      </ul>

      <Footer />
    </div>
  );
}

export default App;
